@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200&family=Nunito:ital,wght@0,200;0,300;0,500;0,600;0,700;0,900;1,200;1,400;1,500;1,600;1,800&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');


@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&family=Montserrat:wght@200&family=Nunito:ital,wght@0,200;0,300;0,500;0,600;0,700;0,900;1,200;1,400;1,500;1,600;1,800&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Ubuntu+Mono:ital,wght@0,400;0,700;1,400;1,700&display=swap');


@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base{
  
  html{
    font-family: 'Ubuntu Mono', sans-serif;
  }

  .blinking-cursor:after{
    content:'|';
    @apply text-red-500;
    animation: blink 1s step-start infinite;
  }

  @keyframes blink{
    50% {
      opacity:0;
    }
  }
}